<template>
    <div class="discover-index">

        <div class="data">
            <div class="tabs">
                <van-tabs v-model="tabs.active" lazy-render swipeable animated sticky color="#FFEA03" :line-height="0" :border="false">
                    <van-tab title="热点" name="topical">
                        <div class="box">
                            <van-list v-model="discover.topical.loading" :finished="discover.topical.finished" finished-text="" @load="initTopical">
                                <div class="item" v-for="(item,index) in discover.topical.data" :key="index">
                                    <div class="video" @click="videoHandel(item.uuid)">
                                        <div class="image">
                                            <img :src="item.thumbnail" alt="">
                                        </div>
                                        <div class="title">
                                            {{ item.translate_title }}
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                    <van-tab title="推荐" name="recommend">
                        <div class="box">
                            <van-list v-model="discover.recommend.loading" :finished="discover.recommend.finished" finished-text="" @load="initRecommend">
                                <div class="item" v-for="(item,index) in discover.recommend.data" :key="index">
                                    <div class="video" @click="videoHandel(item.uuid)">
                                        <div class="image">
                                            <img :src="item.thumbnail" alt="">
                                        </div>
                                        <div class="title">
                                            {{ item.translate_title }}
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                    <van-tab title="最新" name="newest">
                        <div class="box">
                            <van-list v-model="discover.newest.loading" :finished="discover.newest.finished" finished-text="" @load="initNewest">
                                <div class="item" v-for="(item,index) in discover.newest.data" :key="index">
                                    <div class="video" @click="videoHandel(item.uuid)">
                                        <div class="image">
                                            <img :src="item.thumbnail" alt="">
                                        </div>
                                        <div class="title">
                                            {{ item.translate_title }}
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
        </div>

        <tabbar/>

    </div>
</template>

<script>
    import tabbar from '../../components/tabbar';

    import {Icon, Tab, Tabs, List} from 'vant';

    export default {
        components: {
            tabbar,
            [Icon.name]: Icon,
            [Tab.name]: Tab,
            [Tabs.name]: Tabs,
            [List.name]: List
        },
        data() {
            return {
                tabs: {
                    active: 'recommend'
                },
                discover: {
                    topical: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    recommend: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    },
                    newest: {
                        data: [],
                        loading: false,
                        finished: false,
                        empty: false,
                        page: 1
                    }
                }
            }
        },
        mounted() {

        },
        methods: {
            initTopical() {
                this.$axios.get('/api/app/discover/data', {
                    channel: 'topical',
                    page: this.discover.topical.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.discover.topical.page++;
                            if (this.discover.topical.page > result.last_page) {
                                this.discover.topical.finished = true;
                            }
                            this.discover.topical.data = this.discover.topical.data.concat(result.data);
                            if (this.discover.topical.data.length === 0) {
                                this.discover.topical.empty = true;
                            }
                            this.discover.topical.loading = false;
                        }
                    });
            },
            initRecommend() {
                this.$axios.get('/api/app/discover/data', {
                    channel: 'recommend',
                    page: this.discover.recommend.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.discover.recommend.page++;
                            if (this.discover.recommend.page > result.last_page) {
                                this.discover.recommend.finished = true;
                            }
                            this.discover.recommend.data = this.discover.recommend.data.concat(result.data);
                            if (this.discover.recommend.data.length === 0) {
                                this.discover.recommend.empty = true;
                            }
                            this.discover.recommend.loading = false;
                        }
                    });
            },
            initNewest() {
                this.$axios.get('/api/app/discover/data', {
                    channel: 'newest',
                    page: this.discover.newest.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.discover.newest.page++;
                            if (this.discover.newest.page > result.last_page) {
                                this.discover.newest.finished = true;
                            }
                            this.discover.newest.data = this.discover.newest.data.concat(result.data);
                            if (this.discover.newest.data.length === 0) {
                                this.discover.newest.empty = true;
                            }
                            this.discover.newest.loading = false;
                        }
                    });
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            }
        }
    }
</script>

