    }

    .discover-index .data .tabs .van-tabs__nav .van-tab {
        flex: none;
        font-size: 32px;
        padding: 0 20px;
    }

    .discover-index .data .tabs .van-tab--active {
        font-weight: 500;
        font-size: 32px;
