    .discover-index .data .tabs .box .item {
        width: 710px;
        margin: 0 auto 40px auto;
        border-radius: 10px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        overflow: hidden;
    }

    .discover-index .data .tabs .box .item .image {

